<script>
  import cityOptions from '../../assets/js/data'
  import '@/assets/css/comment.css'
  import '@/assets/css/HeelTour.css'
  import ProvincesOfChina from './../public/ProvincesOfChina.vue'
  import {mapGetters} from 'vuex'
  import calendar from './../public/calendar.vue'
  import $ from 'jquery'

  export default {
    name: '',
    components: {
      calendar,
      ProvincesOfChina
    },
    computed: mapGetters([
      'productDetailsObj',
      'lineScheduleObj',
      'IncludeList',
      'noWrapMoneyList',
      'lineMenuList',
      'timesPriceList',
      'priceObj',
      'newPriceDetail',
      'getProvinceList',
      'getCityList',
      'getCountyList',
      'getLineCityList',
    ]),
    data() {
      return {
        price:'',
        n: 0,
        centerDialogVisible: false,//
        showCalendar: false,
        pictureList: [],
        isActiveSearchMonth: false,
        isActive: false,
        isSalendar: true,
        iswrap: true,
        data: [],
        arr1: [],
        arr2: [],
        arr3: [],//
        arr4: [],
        arr5: [],//
        selectMonth: [],
        city: '',
        isScheduleList: false,
        positions: {
          left: 0,
          top: 0
        },
        searchMonth: [],
        date: '',
        userSearch: {
          name: ''
        },
        id: '',
        changeDate: '',//
        cityValue: '',
        m: '',//
        countyValue: '',
        addOrderOptions: {
          ts_pp_ID: '',
          provinceValue: '',
          DayValue: '',
          adultNumber: 0,//
          adultYu: 0,//
          adultPrice: '',//
          childPrice: '',//
          childYu: 0,//
          childNumber: 0,//
        },
      }
    },
    methods: {
      //,item
      changeTime(item) {
        //
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "productPriceID": item.ts_pp_ID
        };
        this.addOrderOptions.ts_pp_ID = item.ts_pp_ID;
        this.addOrderOptions.DayValue = item.ts_pp_Date;
        this.addOrderOptions.adultPrice = item.ts_pp_Price
        this.addOrderOptions.childPrice = item.ts_pp_ChildPrice

        this.$store.dispatch('GetFreeSeat',options)
        .then((data)=>{
          this.addOrderOptions.adultYu = data.fullNo
          this.addOrderOptions.childYu = data.childNo
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        })
      },
      //
      changeSearchMonth(item) {
        this.n = item.i;
        this.changeDate = item.date;
        this.m = item.m
        if (this.addOrderOptions.provinceValue) {
          this.getCitySearch(this.id, this.addOrderOptions.provinceValue, '', item.date, item.m-1)
        } else {
          this.$notify({
            message: '!',
            type: 'error'
          });
        }

//        this.n = index
//        let lis = this.$refs.monthSelecte.querySelectorAll('li');
//        let len = lis.length;
//        for (let i = 0; i < len; i++) {
//          lis[i].querySelector('a').className = ''
//        }
//        lis[index].querySelector('a').className = 'active'
      },
      //
      getCitySearch(id, city, isOne, date, m) {
        $.getScript('http://int.dpool.sina.com.cn/iplookup/iplookup.php?format=js', () => {
          this.userSearch.name = remote_ip_info.city + '';
          if (isOne) {
            this.addOrderOptions.provinceValue = remote_ip_info.city + ''
          }
//
          let options = {
            "loginUserID": "huileyou",
            "loginUserPass": "123",
            "operateUserID": "",
            "operateUserName": "",
            "pcName": "",
            "lineID": id,
            monthChose: date ? date : '',
            "city": city ? city : remote_ip_info.city + ''//remote_ip_info.city +
          };
          this.isSalendar = true;
          this.$store.dispatch('initTimesPrice', options)
            .then((data) => {
              this.isSalendar = false;
              var calendarContentList = document.getElementById("calendarContentList");
              var str = '';
              var year = new Date().getFullYear();
              var month = new Date().getMonth();
              this.showCalendar = true;
              if (!date) {
                this.get(year, month, this, data).then(() => {
                  this.showCalendar = false;
                });
              } else {
                this.get(year, m, this, data).then(() => {
                  this.showCalendar = false;
                });
              }

              let index = Infinity;
              for (var i = 0; i < data.length; i++) {
                if (data[i].ts_pp_Price < index) {
                  index = data[i].ts_pp_Price
                }
              }
              for (var i = 0; i < 4; i++) {
                this.selectMonth.push({
                  year,
                  month: month + 1 + i,
                  index
                })
              }
            })
        });
      },
      //
      getSearchCity() {
        let date = new Date()
        let str = date.getFullYear()+'-'+this.getNum(date.getMonth()+1)+'-'+this.getNum(date.getDate())
        if (this.changeDate) {
          this.getCitySearch(this.id, this.addOrderOptions.provinceValue, '', this.changeDate, this.m);
        } else {
          this.getCitySearch(this.id, this.addOrderOptions.provinceValue, '',str);
        }
        this.addOrderOptions.DayValue = '';
        this.addOrderOptions.adultNumber = 0;//
        this.addOrderOptions.adultYu = 0;//
        this.addOrderOptions.adultPrice = 0;//
        this.addOrderOptions.childPrice = 0;//
        this.addOrderOptions.childYu = 0;//
        this.addOrderOptions.childNumber = 0//
      },
      //0
      getNum(num) {
        return num < 10 ? '0' + num : '' + num;
      },
      //
      async initData() {
        let date = new Date()
        let m = date.getMonth() + 1;
        let y = date.getFullYear()
        for (var i = 0; i < 4; i++) {
          this.searchMonth.push({
            i,
            date: y + '-' + this.getNum(m + i) + '-01',
            name: y + '' + this.getNum(m + i) + '',
            m: m + i
          })
        }
        let tradeID = this.$route.params.id
        //
        var getTradeGoodInfo = {
