      },
      //
      changeType(index) {
        let spans = this.$refs.headerNav.querySelectorAll('span');
        let len = spans.length;

        for (let i = 0; i < len; i++) {
          spans[i].className = ''
        }
        spans[index].className = 'active'
        if (index == 1) {
          this.isScheduleList = true
        } else {
          this.isScheduleList = false
        }
